* {
  box-sizing: border-box;
}

body,
html {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: Montserrat, sans-serif;
}

a {
  text-decoration: none;
  color: inherit;
}

h1 {
  word-break: break-all;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
  max-width: 100%;
}

pre {
  background-color: rgb(242, 242, 242);
  padding: 1rem;
  border-radius: 3px;
  overflow: scroll;
}

p {
  line-height: 1.5;
}

.external-link {
  color: blue;
  margin: 0.3rem 0;
  display: inline-block;
}

.root {
  height: 100%;
}

:root {
  --main-color: #45c88a;
  --secondary-color: #26485b;
  --header-color: #1d1d1d;
  --frontpage-text-color: #fff;
  --dark-color: #2d2d2d;
  --light-green: #6afcac;
}

.frontpage--cover {
  background-image: linear-gradient(to bottom, var(--main-color), var(--secondary-color));
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.frontpage--container {
  -webkit-transform: translateY(-100px);
          transform: translateY(-100px);
}

.frontpage--header {
  font-weight: bold;
  color: var(--frontpage-text-color);
  font-size: 2.5rem;
}

.frontpage--copy {
  padding: 1rem 2rem;
  background-color: var(--secondary-color);
  border-radius: 3px;
  color: var(--frontpage-text-color);
}

.frontpage--copy--paragraph {
  margin: 0;
}

.frontpage--button--container {
  display: -webkit-flex;
  display: flex;
  margin: 1.5rem 0;
  width: 380px;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.frontpage--button {
  background-color: #1b3744;
  color: var(--frontpage-text-color);
  height: 50px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  width: 180px;
  font-size: 1rem;
  font-weight: bold;
  border-radius: 3px;
}

.frontpage--button--icon {
  width: 15px;
  margin-right: 5px;
}

@media (max-width: 450px) {
  .frontpage--container {
    max-width: 90%;
  }
}
.button {
  border: none;
  cursor: pointer;
}

.header {
  width: 100%;
  padding: 0.7rem 1rem;
  background-color: var(--main-color);
  display: -webkit-flex;
  display: flex;
  color: var(--frontpage-text-color);
  -webkit-align-items: center;
          align-items: center;
      position: fixed;
      z-index: 9999;
}

.header--logo {
  width: 60px;
  height: 60px;
  margin-right: 10px;
}

.header--logo--text {
  font-size: 1.5rem;
}

.header--logo-link {
  display: -webkit-flex;
  display: flex;
}

.menu--button {
  display: none;
}

.menu--button--icon {
  width: 25px;
}

.header--link {
  margin-left: auto;
  border: 1px solid #fff;
  padding: 0.8rem;
  border-radius: 3px;
  font-weight: bold;
}

@media(max-width: 1150px) {
  .menu--button {
    display: block;
    margin-left: auto;
    border: none;
    background: transparent;
  }

  .header--link {
    display: none
  }
}
.sidebar {
  height: 100%;
  background-color: var(--secondary-color);
  width: 400px;
  position: fixed;
  z-index: 999;
  top: 83px;
}

.sidebar--container {
  color: var(--frontpage-text-color);
  padding: 1.5rem;
  text-align: left;
  position: relative;
}

.sidebar--header {
  font-size: 1.5rem;
}

.sidebar--subheader {
  font-size: 1.2rem;
  font-weight: normal;
}

.sidebar--links {
  list-style-type: none;
}

.sidebar--links li {
  padding: 0.3rem;
  margin: 0.5rem 0;
}

.sidebar--close-button {
  display: none;
}

.sidebar--copy {
  position: absolute;
  background-color: rgb(23, 46, 58);
  padding: 0rem 1rem;
  border-radius: 3px;
  color: rgb(255, 255, 255);
  position: fixed;
  bottom: 100px;
  width: 350px;
}

.sidebar--close-button--icon {
  width: 25px;
  height: 25px;
}

@media (max-width: 1150px) {
  .sidebar {
    position: fixed;
    top: 0;
    bottom: 0;
    -webkit-transform: translateX(-500px);
            transform: translateX(-500px);
    transition: -webkit-transform 0.5s ease;
    transition: transform 0.5s ease;
    transition: transform 0.5s ease, -webkit-transform 0.5s ease;
    z-index: 9999;
  }

  .sidebar--open {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }

  .sidebar--close-button {
    display: block;
    position: absolute;
    right: 6px;
    padding: 1rem;
    border: none;
    background-color: transparent;
    color: #fff;
  }
}

@media (max-width: 450px) {
  .sidebar {
    width: 100%;
  }

  .sidebar--copy {
    left: 0;
    right: 0;
    margin: 0 auto;
  }
}

.overview {
  text-align: left;
  max-width: 600px;
  padding-bottom: 1rem;
}

.propscard {
  background-color: var(--dark-color);
  border-radius: 3px;
  padding: 0.5rem 1.5rem;
  color: #fff;
  margin: 1rem 0;
}

.propscard--title {
  color: var(--light-green);
  font-size: 1.1rem;
}

.animateHeightDocs {
  text-align: left;
  max-width: 600px;
  height: 100%;
}

.animateHeightInfo {
  max-width: 600px;
}

.exampleOneImage {
  width: 100%;
}

.fadeInDocs {
  text-align: left;
  max-width: 600px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 100%;
}

.animateOnMountDocs {
  text-align: left;
  max-width: 600px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 100%;
}

.animateHeightBySacrificingPerf {
  text-align: left;
  max-width: 600px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 100%;
}

.docs--container {
  height: 100%;
}

.docs--inner--container {
  height: 100%;
  display: -webkit-flex;
  display: flex;
}

.docs--content {
  padding: 2rem 4rem;
  width: 100%;
  margin-left: 400px;
  height: 100%;
  margin-top: 83px;
}

@media (max-width: 1150px) {
  .docs--content {
    margin-left: 0;
  }
}

@media(max-width: 450px) {
  .docs--content {
      padding: 1rem 2rem;
  }
}

.App {
  text-align: center;
  height: 100%;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

